// Colors
$_primary-color: #1C8CA8; // should match the default color of your ng-material default theme's 'primary' palette

$color: (
  'primary': $_primary-color,
  'accent-1': #F05843, // should match the default color of your ng-material default theme's 'accent' palette
  'accent-2': #795C3A,
  'accent-3': #CAD266,
  'warn': #D50000, // should match the default color of your ng-material default theme's 'warn' palette
  'info': #FFA000,
  'success': #00C853,
  'gray-lightest': #f7f7f7,
  'gray-lighter': #eeeeee,
  'gray-light': #dddddd,
  'gray': #cccccc,
  'gray-dark': #aaaaaa,
  'gray-darker': #777777,
  'gray-darkest': #333333,
  'text': rgba(0, 0, 0, 0.87),
  'text-secondary': rgba(0, 0, 0, 0.54),
  'text-disabled': rgba(0, 0, 0, 0.26),
  'text-light': rgba(255, 255, 255, 0.87),
  'text-light-secondary': rgba(255, 255, 255, 0.54),
  'text-light-disabled': rgba(255, 255, 255, 0.26),
  'selected-bg': lighten($_primary-color, 59%),
  'score': #FFC107
);

//'warn': #F44336, // should match the default color of your ng-material default theme's 'warn' palette
//'success': #62C766,

$body-color: (
  'body': map-get($color, 'text'),
  'body-bg': map-get($color, 'gray-lighter')
);

$colors: map-merge($color, $body-color);

// Typography
$baseline-grid:            8px;
$body-font-size-base:      rem(1.500);
$caption-font-size-base:   rem(1.300);

// Layout
$wise-toolbar-height: 42px;

// Menus
$menu-border-radius: 2px;
$max-visible-items: 6;
$menu-item-height: 6 * $baseline-grid !default;
$dense-menu-item-height: 4 * $baseline-grid !default;
$max-menu-height: 2 * $baseline-grid + $max-visible-items * $menu-item-height !default;
$max-dense-menu-height: 2 * $baseline-grid + $max-visible-items * $dense-menu-item-height !default;

// Cards
$card-border-radius: 4px;

// Buttons
$button-border-radius: 3px;
